import React, {useState} from "react";
import { Modal, Carousel } from "react-bootstrap";

export default function ModalImg (props) {

    const { auto, show, handleClose } = props;

    const [render, setRender] = useState();

    const mapeoCarrousel = () => {

        let mappingReturn = [];

        if(auto?.auto === "Seminuevos"){
            mappingReturn = ([0,1,2,3,4]).map((obj, i) => {
                const imagen = `https://riverorenta.mx/seminuevos/images/vista-360/${auto.id}/imagen_${i + 1}.jpg`;
                return(
                    <Carousel.Item key={i}>
                        <img src={imagen} style={{width: '100%', height: '100%'}} alt="Presentacin de carro"/>
                    </Carousel.Item>
                )
            })

        } else if (auto?.auto === "Nuevos") {
            mappingReturn = auto.galeria.map((obj, i) => {
                return(
                    <Carousel.Item key={i}>
                        <img src={obj} style={{width: '100%', height: '100%'}} alt="Presentacin de carro"/>
                    </Carousel.Item>
                )
            })
        }

        return mappingReturn
    }

   

    return(
        <Modal show={show} size="xl" onHide={handleClose}>
            <Modal.Header closeButton></Modal.Header>
            <Modal.Body>
                <Carousel>
                    {mapeoCarrousel()}
                </Carousel>
            </Modal.Body>
        </Modal>
    )

}