class RiveroApi {

  static _api = null;

  static get instance() {
    if (RiveroApi._api === null) {
      RiveroApi._api = new RiveroApi();
    }
    return RiveroApi._api;
  }

  constructor() {
      this.baseURL = 'https://api.gruporivero.com/v1';
  }

  async getNuevos(o){

      let url = `/cars?`;

      if (o && o.search) {
        url += `search=${o.search}&`;
      }
  
      if (o && o.orderBy) {
        url += `orderBy=${o.orderBy}&`;
      }

      if (o && o.unity) {
        //const value = encodeURI(o.make.join(','));
        url += `filter[type]=${o.unity}&`;
      }

      if (o && o.make) {
        //const value = encodeURI(o.make.join(','));
        url += `filter[make]=${o.make}&`;
      }

      if (o && o.year) {
        //const value = encodeURI(o.make.join(','));
        url += `filter[year]=${o.year}&`;
      }

      if (o && o.filter && o.filter.type) {
        const value = encodeURI(o.filter.type);
        url += `filter[type]=${value}&`;
      }

      if (o && o.perPage) {
        url += `per_page=${o.perPage}&`;
      }

      if (o && o.mostRecent) {
        url += `most_recent=1&`;
      }
      console.log("Tengo la url: ", url);
      let nuevos = await fetch(this.baseURL+url);

      let conditionalArray = "";

      if( o.type === "Nuevos" || o.type === "" ) {
        conditionalArray = nuevos.json();
      } else if (o.type === "Seminuevos") {
        conditionalArray = [];
      }

      return conditionalArray;
  }

  async getCarroVersions(data) {
    
    let carro = await fetch(this.baseURL+`/cars/${data}`);

    return carro.json();

  }

  async getSeminuevos(o){

      let url = `/used-cars?`;

      if (o && o.search) {
        url += `search=${o.search}&`;
      }
  
      if (o && o.orderBy) {
        url += `orderBy=${o.orderBy}&`;
      }

      if (o && o.make) {
        //const value = encodeURI(o.make.join(','));
        url += `filter[make]=${o.make}&`;
      }

      if (o && o.year) {
        //const value = encodeURI(o.make.join(','));
        url += `filter[year]=${o.year}&`;
      }

      if (o && o.filter && o.filter.type) {
        const value = encodeURI(o.filter.type);
        url += `filter[type]=${value}&`;
      }

      if (o && o.perPage) {
        url += `per_page=${o.perPage}&`;
      }

      if (o && o.mostRecent) {
        url += `most_recent=1&`;
      }

      let seminuevos = await fetch(this.baseURL+url);

      let conditionalArray = "";

      if( o.type === "Seminuevos" || o.type === "" ) {
        conditionalArray = seminuevos.json();
      } else if (o.type === "Nuevos") {
        conditionalArray = [];
      }

      return conditionalArray;
  }

  async getCarsNissan(o){

    let url = `/cars?`;

    if (o && o.search) {
      url += `search=${o.search}&`;
    }

    if (o && o.orderBy) {
      url += `orderBy=${o.orderBy}&`;
    }

    if (o && o.unity) {
      //const value = encodeURI(o.make.join(','));
      url += `filter[type]=${o.unity}&`;
    }

    if (o && o.make) {
      //const value = encodeURI(o.make.join(','));
      url += `filter[make]=${o.make}&`;
    }

    if (o && o.year) {
      //const value = encodeURI(o.make.join(','));
      url += `filter[year]=${o.year}&`;
    }

    if (o && o.filter && o.filter.type) {
      const value = encodeURI(o.filter.type);
      url += `filter[type]=${value}&`;
    }

    if (o && o.perPage) {
      url += `per_page=${o.perPage}&`;
    }

    if (o && o.mostRecent) {
      url += `most_recent=1&`;
    }
    console.log("Tengo la url: ", url);
    let nuevos = await fetch(this.baseURL+url);

    let conditionalArray = "";

    if( o.type === "Nuevos" || o.type === "" ) {
      conditionalArray = nuevos.json();
    } else if (o.type === "Seminuevos") {
      conditionalArray = [];
    }

    return conditionalArray;

  }

  async getCarMakes() {

    let makes = await fetch(this.baseURL+`/used-cars-makes`);

    return makes.json();

  }

}

module.exports = RiveroApi.instance;