import React from "react";
import { Container, Row, Col, Dropdown, Form, Accordion, Button } from "react-bootstrap";

export default function FilterLayout ({datosFiltros, Buscar, onFilter, setSearch}){

    return(
        <>
            <Container className="p-2 text-white">
                <center>Filtro</center>
            </Container>
            <Container>
                <Form.Control placeholder="Buscar..."  onChange={(e) => setSearch(e.target.value)}/>
            </Container>
            <Accordion alwaysOpen>
                <Accordion.Item eventKey="0">
                    <Accordion.Header>Inventario</Accordion.Header>
                    <Accordion.Body>
                    <div className="px-3 p-1"><Form.Check onClick={(e) => onFilter("Tipo", e.target.id)} type="checkbox" id={`Nuevos`} label={`Nuevos`} className="FormCheck"/></div>
                    <div className="px-3 p-1"><Form.Check onClick={(e) => onFilter("Tipo", e.target.id)} type="checkbox" id={`Seminuevos`} label={`Seminuevos`} /></div>
                    </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="1">
                    <Accordion.Header>Unidad</Accordion.Header>
                    <Accordion.Body>
                        <div className="px-3 p-1"><Form.Check type="checkbox" onClick={(e) => onFilter("Unidad", e.target.id)} id={`suvs`} label={`Suv's`} className="FormCheck"/></div>
                        <div className="px-3 p-1"><Form.Check type="checkbox" onClick={(e) => onFilter("Unidad", e.target.id)} id={`autos`} label={`Autos`} /></div>
                        <div className="px-3 p-1"><Form.Check type="checkbox" onClick={(e) => onFilter("Unidad", e.target.id)} id={`pickup`} label={`PickUp`} className="FormCheck"/></div>
                        <div className="px-3 p-1"><Form.Check type="checkbox" onClick={(e) => onFilter("Unidad", e.target.id)} id={`deportivo`} label={`Deportivos y Elctrico`} /></div>
                    </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="2">
                    <Accordion.Header>Marca</Accordion.Header>
                    <Accordion.Body>
                        {
                            datosFiltros.marcas?.map((obj, i) => {
                                return(
                                <div className="px-3 p-1" key={i}><Form.Check onClick={(e) => onFilter("Marca", e.target.id)} type="checkbox" id={obj} label={obj} /></div>
                                )
                            })
                        }
                    </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="3">
                    <Accordion.Header>Modelo</Accordion.Header>
                    <Accordion.Body>
                        {
                            datosFiltros.modelo?.map((obj, i) => {
                            return(
                                <div className="px-3 p-1" key={i}><Form.Check onClick={(e) => onFilter("Modelo", e.target.id)} type="checkbox" id={obj} label={obj} /></div>
                            )
                            })
                        }
                    </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="4">
                    <Accordion.Header>Ao</Accordion.Header>
                    <Accordion.Body>
                        {
                            datosFiltros?.anios?.map((obj, i) => {
                                return(
                                <div className="px-3 p-1" key={i}><Form.Check onClick={(e) => onFilter("Anio", e.target.id)} type="checkbox" id={obj} label={obj} /></div>
                                )
                            })
                        }
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
            <Container className="text-center p-3">
                <Button onClick={() => Buscar()}>Filtrar</Button>
            </Container>
        </>
    )

}