import React, {useState} from "react";
import { Container, Row, Col, Image, Button, Table, Card } from "react-bootstrap";
import PlaceHolderCar from "./placeholderCar";
import { navigate } from "gatsby";
import "./listcss.css";

import ModalImg from "../ModalImg";

export default function GridView ( props ) {

    const { car } = props;

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
  
    //console.log("Estamos en el listView: ", nuevos);

    function modalFotos(e){
        console.log("Pulsado modalFotos", e)
        setShow(true);
    }

    function verDetalle(e){
        console.log("Pulsado verDetalles", e);

        if(e.auto === "Seminuevos"){
            navigate('https://www.gruporivero.com/seminuevos/'+e.slug);
        } else if(e.auto === "Nuevos") {
            navigate('https://www.gruporivero.com/'+e.slug);
        }

    }

    return(
        <>
            <Card style={{ marginBottom: 20}}>
                <Card.Header className="bg-dark">
                    <div className="divImagen" style={{backgroundColor: 'transparent' , height: "100%" }}>
                        <Image src={car?.imagen || car?.picture} style={{width: '100%'}}/>
                    </div>
                </Card.Header>
                <Card.Body>
                    <Card.Title>{car?.marca || car?.make} {car?.nombre || car?.model} {car?.year || car?.year}</Card.Title>
                    <Row>
                            <Col>
                                <Table striped bordered hover>
                                    <tbody>
                                        <tr>
                                            <td>Auto</td>
                                            <td>{car?.auto}</td>
                                        </tr>
                                        <tr>
                                            <td>Transmisin o Version</td>
                                            <td>{car?.trans || car?.name }</td>
                                        </tr>
                                        <tr>
                                            <td>Color</td>
                                            <td>{car?.color || car?.color}</td>
                                        </tr>
                                    </tbody>
                                </Table>
                            </Col>
                            <Col>
                                <Table striped bordered hover>
                                    <tbody>
                                        <tr>
                                            <td>Precio</td>
                                            <td>$ {car?.precio || car?.price}</td>
                                        </tr>
                                    </tbody>
                                </Table>
                                <Row>
                                    <Button onClick={() => verDetalle(car)}>Ver ms detalles</Button>
                                </Row>
                            </Col>
                        </Row>
                        <Row className="pt-2">
                            <Button onClick={() => setShow(true)}>Fotos</Button>
                        </Row>
                </Card.Body>
            </Card>
          {<ModalImg show={show} auto={car} handleClose={handleClose}/>}
        </>
    )

}